@import url("https://fonts.googleapis.com/css2?family=Architects+Daughter&display=swap");

.App {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin: 15px;
}

body {
  background-color: black;
  color: rgb(235, 235, 235);
  font-family: "Helvetica";
}

h1 {
  margin: 1em 0 0 0;
  font-size: 2rem;
  color: #6141ff;
}

h2 {
  margin-bottom: 1em;
  text-align: center;
}

.radio {
  max-width: 60em;
  width: 100%;

}

.filters {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-bottom: 1em;
  margin-top: 1em;
  font-size: 1.2rem;
  width: 100%;



  span {
    margin: 0.25em;
    border-radius: 10px;
    padding: 0.25em 0.75em;
    border:2px solid #3700ff;
    transition-duration: 1s;

    &:hover {
      cursor: pointer;
      border-color: #9ecaed;
      box-shadow: 0 0 5px #9ecaed;
    }
  }

  .selected {
    border-color: #9ecaed;
    box-shadow: 0 0 15px #9ecaed;

  }
}

.stations {
  display: grid;
  grid-template-columns: repeat(3, 33%);
  width: 100%;
}

.station {
  font-size: 1.2em;
  border: 2px solid #3700ff;
  margin: 0.25em;
  border-radius: 10px;
  padding: 1em;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition-duration: 1s;

  &:hover {
    border-color: #9ecaed;
  }
}

.stationName {
  display: flex;
  flex-direction: row;
  margin-bottom: 1em;
  width: 100%;
  align-items: center;
  justify-content: start;
}

.name {
  width: 100%;
  text-align: center;
}

img {
  display: block;
  width: 3.5em;
  height: 3.5em;
  border-radius: 50%;
  border: none;
  box-shadow: 0 0 15px #9ecaed;
  margin: 0 0.25em;
}

audio {
  width: 100%;
}

.rhap_container.player {
  background-color:black;
  border: 1px solid #3700ff;
  display: flex;
  justify-items: center;
  padding: 0.25em 0.75em;
  border-radius: 10px;
  height: 6rem;
  text-align: center;
  color: gold;
}

.rhap_stacked .rhap_controls-section {
  margin-top: 0 !important;

}

.rhap_controls-section .rhap_volume-controls {
  justify-content: right;
}

.rhap_controls-section .rhap_main-controls button[aria-label="Play"] svg path {
  fill: rgb(235, 235, 235) !important;
}

.rhap_controls-section .rhap_main-controls button[aria-label="Pause"] svg path {
  fill: gold !important;
}

@media only screen and (max-width: 600px) {
  .stations {
    grid-template-columns: repeat(2, 50%);
  }

  img {
    width: 7em;
    height: 7em;
    margin-bottom:3em;
  }
  .stationName {
    display: flex;
    flex-direction: column;
    margin-bottom: 1em;
    width: 100%;
    align-items: center;
    justify-content: start;
    &:hover {
      border-color: #9ecaed;
    }
  }
}